import image from "../../assets/images/img-placeholder.webp";
import "./board-of-directors.css";

const directors = [
  { name: "John Doe", position: "Director", thumbnail: image },
  { name: "John Doe", position: "Director", thumbnail: image },
  { name: "John Doe", position: "Director", thumbnail: image },
  { name: "John Doe", position: "Director", thumbnail: image },
  { name: "John Doe", position: "Director", thumbnail: image },
  { name: "John Doe", position: "Director", thumbnail: image },
  { name: "John Doe", position: "Director", thumbnail: image },
  { name: "John Doe", position: "Director", thumbnail: image },
  { name: "John Doe", position: "Director", thumbnail: image },
  { name: "John Doe", position: "Director", thumbnail: image },
];

const BoardOfDirectors = () => {
  return (
    <section className="board-of-directors bg-white">
      <div className="container">
        <h2>Students For Liberty</h2>
        <h3>Board Of Directors</h3>
        <div className="directors-grid-wrapper">
          {directors.map((director, i) => (
            <div key={i} className="director-card">
              <img
                src={director.thumbnail}
                alt={`${director.name}'s thumbnail`}
              />
              <h4>{director.name}</h4>
              <p>{director.position}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default BoardOfDirectors;
