import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper/modules";
import "swiper/css/bundle";
import "./slider-invest-in-liberty.css";
import image from "../../assets/images/img-placeholder.webp";

const investInLibertySlides = [
  {
    image: image,
  },
  {
    image: image,
  },
  {
    image: image,
  },
  {
    image: image,
  },
  {
    image: image,
  },
  {
    image: image,
  },
];

const SliderInvestLiberty = () => {
  return (
    <Swiper
      modules={[Autoplay]}
      spaceBetween={50}
      slidesPerView={4}
      loop={true}
      centeredSlides={true}
      autoplay={{ delay: 2000, pauseOnMouseEnter: true }}
    >
      {investInLibertySlides.map((slide, i) => (
        <SwiperSlide key={i} className="slide-invest">
          <img src={slide.image} />
        </SwiperSlide>
      ))}
    </Swiper>
  );
};

export default SliderInvestLiberty;
