import { useRef, useState } from "react";
import image from "../../assets/images/img-placeholder.webp";
import "./global-presence.css";

const GlobalPresence = () => {
  const [activeTab, setActiveTab] = useState("North America");

  const tabs = [
    "North America",
    "Asia Pacific",
    "Europe",
    "Africa",
    "Eslibertad",
    "MENA",
    "South Asia",
    "Brasil",
  ];
  const tabsContentRef = useRef({
    "North America": {
      name: "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image3: image,
    },
    "Asia Pacific": {
      name: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3:
        "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image3: image,
    },
    Europe: {
      name: "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image3: image,
    },
    Africa: {
      name: "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image3: image,
    },
    Eslibertad: {
      name: "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image3: image,
    },
    MENA: {
      name: "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image3: image,
    },
    "South Asia": {
      name: "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image3: image,
    },
    Brasil: {
      name: "Overturning of a ban on pepper spray on campus in TN in order to protect women from rape",
      image: image,
      name2: "LibertyCon International was a major success",
      image2: image,
      name3: "Dartmouth Free Speech Summit: Students Champion Open Dialogue",
      image3: image,
    },
  });

  const activeTabContent = tabsContentRef.current[activeTab];

  return (
    <section className="global-presence bg-grey">
      <div className="container">
        <h2>Global presence</h2>
        <h3>Students For Liberty impact around the globe</h3>
        <div className="tabs-wrapper">
          <ul className="tabs">
            {tabs.map((tab) => (
              <li
                key={tab}
                onClick={() => setActiveTab(tab)}
                className={activeTab === tab ? "active" : ""}
              >
                {tab}
              </li>
            ))}
          </ul>
          <div className="tab-content">
            <div className="tab-featured-content">
              <img src={activeTabContent.image} />
              <p>{activeTabContent.name}</p>
            </div>
            <div className="tab-secondary-content">
              <div>
                <img src={activeTabContent.image2} />
                <p>{activeTabContent.name2}</p>
              </div>
              <div>
                <img src={activeTabContent.image3} />
                <p>{activeTabContent.name3}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default GlobalPresence;
