import { useEffect, useRef } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Splitting from "splitting";
import { animateTitle3 } from "../../helpers/animateTitle";
import "./year-in-numbers.css";

const YearInNumbers = () => {
  const titleRef = useRef();

  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);
    Splitting();
    animateTitle3(titleRef.current);
  }, []);

  return (
    <section className="year-in-number bg-white">
      <div className="container">
        <h2>Year in numbers</h2>
        <h3 ref={titleRef} data-splitting>
          The largest international pro-liberty student organizayion in the
          world!
        </h3>
        <div className="content-wrapper">
          <div className="numbers-wrapper">
            <div className="number-info-wrapper">
              <p>$14M</p>
              <p>Economic Impact</p>
            </div>

            <div className="number-info-wrapper">
              <p>241%</p>
              <p>
                ROI: each $1
                <br /> $2,41 of Impact
              </p>
            </div>
            <div className="number-info-wrapper">
              <p>100h</p>
              <p>More hours spent training than ever</p>
            </div>
          </div>
          <a href="#">More Numbers </a>
        </div>
      </div>
    </section>
  );
};

export default YearInNumbers;
